    width: 2rem;
    height: 2rem;
  }
  .ivu-table-wrapper{
    .ivu-table-fixed, .ivu-table-fixed-right{
       box-shadow: none !important;
}
height: 600px!important;
.ivu-table-fixed-body{
  height: 600px!important;
}
.ivu-table-overflowY{
  height: 600px!important;
}
.ivu-table {
  background:#FAFFFB!important;
}
}
.table-scroll-mod{
position: relative;
height: 900px;
overflow: hidden;
}
.table-scroll-box{
white-space: nowrap;
overflow: scroll;
height:900px;
z-index: 2001;
}
.table-scroll-box::-webkit-scrollbar{
width: 8px;
height: 8px;
}
.table-scroll-box > table{
overflow-x: scroll;
}
.table-scroll-box > table tr{
display: -webkit-flex;
}
.table-scroll-box .fixed-head{
position: absolute;
top: 0;
left: 0;
z-index: 1000;
background-color:#fff;
/*width: calc(100% - 8px);*/
    width: 100%;
    overflow-x: scroll;
  }
  .table-scroll-box .fixed-head::-webkit-scrollbar{
    width: 0px;
    height: 0px;
  }
  .table-scroll-box .fixed-head >table{
    overflow-x: scroll;
  }
  .table-scroll-box .fixed-head >table tr{
    display: -webkit-flex;
  }
  .fixed-left{
    width:110px;
    height: 900px;
    overflow-y: scroll;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 999;
  }
  .fixed-left > table{
    border-collapse: collapse;
  }
  .fixed-left > table > thead{
    background-color:violet;
    height: 22px;
  }
  .fixed-left > table > thead > th,.fixed-left > table > thead > tr{
    height: 22px;
  }
  .fixed-left > table > tbody> th,.fixed-left > table > tbody > tr{
    height: 24.04px;
  }
  .fixed-left > table > tbody> th,.fixed-left > table > tbody > tr>td,th{
    /*border: 1px solid #ff7414;*/
  }
  .table-scroll-box .fixed-left::-webkit-scrollbar{
    width: 0px;
    height: 0px;
  }
  .fixedTopLeft{
    background-color:#fff;
    height: 22px;
    width: 105px;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1001;
    font-weight: bold;
    line-height: 22px;
  }
  .scrollTable>tbody>tr>td{
    /*border: 1px solid #ff7414;*/
  }
  .ivu-table-cell{
    padding: 8px !important;
    line-height: 1.3;
  }
</style>



